import { zTool } from "zerod";
/**
 *
 * @param {string} method //get || post || put    .....
 * @param {string} url
 * @param {object} query //keyvalue 
 * @param {object} config //config
 */
function httpAjax(method, url, query, config) {
	if (!url.includes("/sys")) url = `/webapi${url}`; //url
	const promise = zTool.httpAjax(method, url, query, config);
	return promise;
}
export default httpAjax;

