//index.jsx
//
// export default {
// 	name: "Zbutton",
// 	HOC: Zbutton,
// };

const components = require.context("./", true, /index.jsx$/);
let loadCompoents = {};
const names = [];
components.keys().forEach((key) => {
	let defines = undefined;
	try {
		defines = components(key).default;
	} catch (e) {
		throw Error(`${key}:${e}`);
	}
	if (defines === undefined || typeof defines !== "object") {
		throw Error(`${key}: export default export default `);
	} else if (typeof defines.HOC != "function") {
		throw Error(`${key}:HOC`);
	}
	let name = defines.name;
	if (typeof name !== "string") {
		throw Error(`${key}:name`);
	}else if(!/^A[A-z0-9]*HOC$/.test(name)){
        throw Error(`${key}:nameAHOC`);
    }
	name = name.trim();
	if (names.includes(name)) {
		throw Error(`${key}:${name}HOC`);
	} else {
		names.push(name);
	}
	loadCompoents[name] = defines.HOC;
});
export default loadCompoents;
