// export default {
//   name: "home",
//   apis: {
//       //
//     getList(query) {
//       return httpAjax("post", "", query);
//     },
//   }
// };
//
// import api from '@/App.api.js'
// api.home.getList()

const components = require.context("./Api", true, /\.api.js$/);
let loadApis = {};
const names = [];
components.keys().forEach((key) => {
	let defines = undefined;
	try {
		defines = components(key).default;
	} catch (e) {
		throw Error(`${key}:${e}`);
	}
	if (defines === undefined || typeof defines !== "object") {
		throw Error(`${key}: export default export default `);
	} else if (typeof defines.apis != "object") {
		throw Error(`${key}:apisobject`);
	}
	let name = defines.name;
	if (typeof name !== "string") {
		throw Error(`${key}:name`);
	}
	name = name.trim();
	if (names.includes(name)) {
		throw Error(`${key}:${name}`);
	} else {
		names.push(name);
	}
	loadApis[name] = defines.apis;
});
export default loadApis;
